"use client";

import { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { SalesService } from "@/services/sales.service";
import EditSaleModal from "@/components/Sales/EditSaleModal";

import toast from "react-hot-toast";
import {
  ArrowLeft,
  Printer,
  XCircle,
  Calendar,
  User,
  Building,
  Package,
  DollarSign,
  AlertTriangle,
} from "lucide-react";

/* =========================
   Types
========================= */
interface SaleItem {
  productName: string;
  quantity: number;
  unitPrice: number;
  lineTotal: number;
}

interface SaleDetail {
  saleId: string;
  invoiceNumber: string;
  customerName: string;
  branchName: string;
  subTotal: number;
  discount: number;
  totalAmount: number;
  paidAmount: number;
  balance: number;
  paymentStatus: "OPEN" | "PARTIAL" | "CLOSED" | "CANCELLED";
  remark?: string;
  saleDate: string;
  items: SaleItem[];
}

interface CancelModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: (remark: string) => void;
  isLoading: boolean;
}

/* =========================
   Cancel Confirmation Modal
========================= */
function CancelModal({ isOpen, onClose, onConfirm, isLoading }: CancelModalProps) {
  const [remark, setRemark] = useState("Customer requested cancellation");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (remark.trim()) {
      onConfirm(remark.trim());
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-xl max-w-md w-full p-6">
        <div className="flex items-center gap-3 mb-4">
          <div className="p-2 bg-red-100 rounded-lg">
            <AlertTriangle className="w-6 h-6 text-red-600" />
          </div>
          <div>
            <h3 className="font-semibold text-lg">Cancel Sale</h3>
            <p className="text-sm text-gray-500">This action cannot be undone</p>
          </div>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Reason for cancellation *
            </label>
            <textarea
              value={remark}
              onChange={(e) => setRemark(e.target.value)}
              className="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-red-500 focus:border-red-500"
              rows={3}
              placeholder="Enter detailed reason for cancellation..."
              required
            />
          </div>

          <div className="flex gap-3 justify-end pt-4 border-t">
            <button
              type="button"
              onClick={onClose}
              disabled={isLoading}
              className="px-4 py-2 text-sm rounded-lg border hover:bg-gray-50 disabled:opacity-50"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={isLoading || !remark.trim()}
              className="px-4 py-2 text-sm rounded-lg bg-red-600 text-white hover:bg-red-700 disabled:opacity-50 flex items-center gap-2"
            >
              {isLoading ? (
                <>
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
                  Cancelling...
                </>
              ) : (
                <>
                  <XCircle className="w-4 h-4" />
                  Confirm Cancellation
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default function SaleDetailPage() {
  const { saleId } = useParams<{ saleId: string }>();
  const router = useRouter();

  const [sale, setSale] = useState<SaleDetail | null>(null);
  const [loading, setLoading] = useState(false);
  const [canceling, setCanceling] = useState(false);
  const [showCancelModal, setShowCancelModal] = useState(false);

  /* =========================
     LOAD SALE DETAIL
  ========================= */
  async function loadSaleDetail() {
    try {
      setLoading(true);
      const res = await SalesService.getSaleDetail(saleId);
      setSale(res.data.data);
    } catch {
      toast.error("Failed to load sale detail");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    if (saleId) loadSaleDetail();
  }, [saleId]);

  /* =========================
     CANCEL SALE
  ========================= */
  async function handleCancelSale(remark: string) {
    if (!sale) return;

    try {
      setCanceling(true);
      await SalesService.cancelSale({
        saleId: sale.saleId,
        remark: remark,
      });

      toast.success("Sale cancelled successfully");
      setShowCancelModal(false);
      loadSaleDetail();
    } catch {
      toast.error("Failed to cancel sale");
    } finally {
      setCanceling(false);
    }
  }

  /* =========================
     PRINT/EXPORT
  ========================= */
  function handlePrint() {
    window.print();
  }

  function handleExportPDF() {
    toast.success("PDF export feature coming soon!");
  }

  /* =========================
     LOADING & ERROR STATES
  ========================= */
  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="w-12 h-12 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Loading sale details...</p>
        </div>
      </div>
    );
  }

  if (!sale) {
    return (
      <div className="max-w-5xl mx-auto p-6">
        <div className="bg-white rounded-xl p-8 text-center">
          <XCircle className="w-16 h-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-xl font-semibold mb-2">No Sale Found</h3>
          <p className="text-gray-500 mb-6">
            The requested sale could not be found or has been deleted.
          </p>
          <button
            onClick={() => router.back()}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 inline-flex items-center gap-2"
          >
            <ArrowLeft className="w-4 h-4" />
            Go Back
          </button>
        </div>
      </div>
    );
  }

  const canCancel =
    sale.paymentStatus === "OPEN" || sale.paymentStatus === "PARTIAL";

  /* =========================
     MAIN COMPONENT
  ========================= */
  return (
    <>
      {/* CANCEL MODAL */}
      <CancelModal
        isOpen={showCancelModal}
        onClose={() => setShowCancelModal(false)}
        onConfirm={handleCancelSale}
        isLoading={canceling}
      />

      <div className="max-w-6xl mx-auto p-4 md:p-6">
        {/* HEADER */}
        <div className="mb-6">
          <div className="flex items-center gap-3 mb-2">
            <button
              onClick={() => router.back()}
              className="p-2 hover:bg-gray-100 rounded-lg"
            >
              <ArrowLeft className="w-5 h-5" />
            </button>
            <h1 className="text-2xl font-bold text-gray-900">Sale Details</h1>
          </div>
          <p className="text-gray-500">View and manage sale information</p>
        </div>

        <div className="grid lg:grid-cols-3 gap-6">
          {/* LEFT COLUMN - INVOICE CARD */}
          <div className="lg:col-span-2 space-y-6">
            {/* INVOICE HEADER */}
            <div className="bg-white rounded-xl border p-6">
              <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 mb-6">
                <div>
                  <h2 className="text-xl font-bold text-gray-900">
                    Invoice #{sale.invoiceNumber}
                  </h2>
                  <div className="flex items-center gap-4 mt-2">
                    <StatusBadge status={sale.paymentStatus} />
                    <div className="flex items-center gap-2 text-gray-500">
                      <Calendar className="w-4 h-4" />
                      <span className="text-sm">
                        {new Date(sale.saleDate).toLocaleDateString("en-US", {
                          weekday: "long",
                          year: "numeric",
                          month: "long",
                          day: "numeric",
                          hour: "2-digit",
                          minute: "2-digit",
                        })}
                      </span>
                    </div>
                  </div>
                </div>

                <div className="flex gap-2">
                  <button
                    onClick={handlePrint}
                    className="px-4 py-2 border rounded-lg hover:bg-gray-50 flex items-center gap-2"
                  >
                    <Printer className="w-4 h-4" />
                    Print
                  </button>
                </div>
              </div>

              {/* CUSTOMER & BRANCH INFO */}
              <div className="grid md:grid-cols-2 gap-6 mb-8">
                <InfoCard
                  icon={<User className="w-5 h-5" />}
                  title="Customer"
                  value={sale.customerName}
                />
                <InfoCard
                  icon={<Building className="w-5 h-5" />}
                  title="Branch"
                  value={sale.branchName}
                />
              </div>

              {/* ITEMS TABLE */}
              <div className="overflow-x-auto">
                <h3 className="font-semibold mb-4 flex items-center gap-2">
                  <Package className="w-5 h-5" />
                  Items ({sale.items.length})
                </h3>
                <table className="w-full">
                  <thead>
                    <tr className="border-b">
                      <th className="text-left py-3 px-4 text-sm font-medium text-gray-500">
                        Product
                      </th>
                      <th className="text-right py-3 px-4 text-sm font-medium text-gray-500">
                        Qty
                      </th>
                      <th className="text-right py-3 px-4 text-sm font-medium text-gray-500">
                        Unit Price
                      </th>
                      <th className="text-right py-3 px-4 text-sm font-medium text-gray-500">
                        Total
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {sale.items.map((item, i) => (
                      <tr key={i} className="border-b hover:bg-gray-50">
                        <td className="py-3 px-4">{item.productName}</td>
                        <td className="py-3 px-4 text-right">{item.quantity}</td>
                        <td className="py-3 px-4 text-right">
                          ${item.unitPrice.toFixed(2)}
                        </td>
                        <td className="py-3 px-4 text-right font-medium">
                          ${item.lineTotal.toFixed(2)}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>

            {/* REMARK SECTION */}
            {sale.remark && (
              <div className="bg-white rounded-xl border p-6">
                <h3 className="font-semibold mb-3">Additional Remarks</h3>
                <div className="p-4 bg-gray-50 rounded-lg">
                  <p className="text-gray-700">{sale.remark}</p>
                </div>
              </div>
            )}
          </div>

          {/* RIGHT COLUMN - SUMMARY & ACTIONS */}
          <div className="space-y-6">
            {/* SUMMARY CARD */}
            <div className="bg-white rounded-xl border p-6">
              <h3 className="font-semibold mb-4 flex items-center gap-2 text-green-700">
                <DollarSign className="w-5 h-5" />
                Payment Summary
              </h3>
              <div className="space-y-3">
                <SummaryRow label="Sub Total" value={`$${sale.subTotal.toFixed(2)}`} />
                <SummaryRow label="Discount" value={`-$${sale.discount.toFixed(2)}`} />
                <div className="border-t pt-3">
                  <SummaryRow
                    label="Total Amount"
                    value={`$${sale.totalAmount.toFixed(2)}`}
                    bold
                  />
                </div>
                <SummaryRow label="Paid Amount" value={`$${sale.paidAmount.toFixed(2)}`} />
                <div className="border-t pt-3">
                  <SummaryRow
                    label="Balance"
                    value={`$${sale.balance.toFixed(2)}`}
                    valueColor={
                      sale.balance === 0
                        ? "text-green-600"
                        : sale.balance > 0
                        ? "text-yellow-600"
                        : "text-red-600"
                    }
                    bold
                  />
                </div>
              </div>
            </div>

            {/* ACTIONS CARD */}
            <div className="bg-white rounded-xl border p-6">
              <h3 className="font-semibold mb-4">Actions</h3>
              <div className="space-y-3">
                <button
                  onClick={() => router.push(`/sales/${saleId}/edit`)}
                  className="w-full px-4 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center justify-center gap-2"
                >
                  Edit Sale
                </button>

                <button
                  onClick={() => router.push(`/sales/${saleId}/payment`)}
                  className="w-full px-4 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 flex items-center justify-center gap-2"
                >
                  Add Payment
                </button>

                {canCancel && (
                  <button
                    onClick={() => setShowCancelModal(true)}
                    className="w-full px-4 py-3 bg-red-50 text-white bg-red-600  flex items-center justify-center gap-2"
                  >
                    Cancel Sale
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

/* =========================
   Helper Components
========================= */
function InfoCard({
  icon,
  title,
  value,
}: {
  icon: React.ReactNode;
  title: string;
  value: string;
}) {
  return (
    <div className="p-4 border rounded-lg">
      <div className="flex items-center gap-3">
        <div className="p-2 bg-blue-50 rounded-lg text-blue-600">{icon}</div>
        <div>
          <p className="text-sm text-gray-500">{title}</p>
          <p className="font-medium">{value}</p>
        </div>
      </div>
    </div>
  );
}

function SummaryRow({
  label,
  value,
  valueColor = "text-gray-900",
  bold = false,
}: {
  label: string;
  value: string;
  valueColor?: string;
  bold?: boolean;
}) {
  return (
    <div className="flex justify-between items-center">
      <span className="text-gray-600">{label}</span>
      <span className={`${bold ? "font-bold" : "font-medium"} ${valueColor}`}>
        {value}
      </span>
    </div>
  );
}

function StatusBadge({
  status,
}: {
  status: "OPEN" | "PARTIAL" | "CLOSED" | "CANCELLED";
}) {
  const config = {
    OPEN: {
      label: "Open",
      className: "bg-blue-100 text-blue-700",
      icon: "●",
    },
    PARTIAL: {
      label: "Partial",
      className: "bg-yellow-100 text-yellow-700",
      icon: "●",
    },
    CLOSED: {
      label: "Paid",
      className: "bg-green-100 text-green-700",
      icon: "✓",
    },
    CANCELLED: {
      label: "Cancelled",
      className: "bg-red-100 text-red-700",
      icon: "✕",
    },
  };

  const { label, className } = config[status];

  return (
    <span
      className={`px-3 py-1 rounded-full text-xs font-semibold inline-flex items-center gap-1.5 ${className}`}
    >
      <span className="text-[10px]">●</span>
      {label}
    </span>
  );
}